<template>
	<div class="data">
		<el-row>
      <el-col :span='24'>
      	<el-form :inline="true" :data="form" class="demo-form-inline" size='mini' >
	      	<el-row :span='24' >
	      	  <el-col :span='5'>
		          <el-form-item label=""  width='100%'>
		            <el-date-picker size='mini' v-model="form.time" type="date" placeholder="选择日期"></el-date-picker>
		          </el-form-item>
	          </el-col>
	          <el-col :span='4'>
		          <el-form-item label="" :inline="true">
		            <el-select v-model="form.grade" placeholder="年级" >
		              <el-option label="四年级" value="四年级"></el-option>
		              <el-option label="五年级" value="五年级"></el-option>
		            </el-select>
		          </el-form-item>
	          </el-col>
	          <el-col :span='4'>
		          <el-form-item label="" :inline="true">
		            <el-select v-model="form.clazz" placeholder="班级" >
		              <el-option label="一班" value="一班"></el-option>
		              <el-option label="二班" value="二班"></el-option>
		            </el-select>
		          </el-form-item>
	          </el-col>
	          <el-col :span='4'>
		          <el-form-item label="" :inline="true">
		            <el-select v-model="form.course" placeholder='课程'> 
		              <el-option label="体育课程 16：00-16:45" value="体育课程 16：00-16:45"></el-option>
		              <el-option label="活动课程 10：00-10:20" value="活动课程 10：00-10:20"></el-option>
		            </el-select>
		          </el-form-item>
			      </el-col>
			      <el-col :span='6'>
		          <el-form-item>
		            <el-button type='info' size='mini' :inline="true">查询</el-button>
		          </el-form-item>
	          </el-col>
          </el-row>
        </el-form>
      </el-col>
    </el-row>
    <el-row>
    	<el-col :span='24'>
    		<el-row type='flex' justify='center'>
    			<span class='title'>班级数据报表</span>
    			<el-row type='flex' justify='end'>
	    			<el-form>
	    				<el-form-item>
	    					<el-button type='info' size='mini'>下载</el-button>
	    				</el-form-item>
	    			</el-form>
    			</el-row>
    		</el-row>
    	</el-col>
    </el-row>
		<el-table :data="classData" style="width:100%;" height='430' size='mini' stripe :header-row-style="rowClass">
	    <el-table-column fixed prop="class" label="班级">
	    </el-table-column>
	    <el-table-column prop="date" label="上课时间">
	    </el-table-column>
	    <el-table-column prop="teacher" label="任课老师">
	    </el-table-column>
	    <el-table-column prop="students" label="上课人数">
	    </el-table-column>
	    <el-table-column prop="lowRate" label="班级最低心率">
	    </el-table-column>
	    <el-table-column prop="averageRate" label="班级平均心率">
	    </el-table-column>
	    <el-table-column prop="heightRate" label="班级最高心率">
	    </el-table-column>
	    <el-table-column prop="closeLowStu" label="接近最低心率人数">
	    </el-table-column>
	    <el-table-column prop="closeHeightStu" label="接近最高心率人数">
	    </el-table-column>
	    <el-table-column prop="overLowRate" label="超过最低心率人数">
	    </el-table-column>
	    <el-table-column prop="overHeightRate" label="超过最高心率人数">
	    </el-table-column>
	    <el-table-column prop="lesson" label="课程">
	    </el-table-column>
	  </el-table>
	</div>
</template>

<script type="text/javascript">
export default {
  name: 'data',
  data () {
    return {
    	form:{},
      classData: [{
	      class:'1班',
	      date: '2017120114:30~15:15',
	      teacher: '王小虎',
	      students: '45',
	      lowRate: '50',
	      averageRate:'79',
	      heightRate: '102',
	      closeLowStu: '2',
	      closeHeightStu:'1',
	      overLowRate:'1',
	      overHeightRate:'1',
	      lesson:'体育课程'
	    },{
	      class:'2班',
	      date: '2017120114:30~15:15',
	      teacher: '王小虎',
	      students: '45',
	      lowRate: '50',
	      averageRate:'79',
	      heightRate: '102',
	      closeLowStu: '2',
	      closeHeightStu:'1',
	      overLowRate:'1',
	      overHeightRate:'1',
	      lesson:'体育课程'
	    }],
    }
  },
  methods:{
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },

  },
}
</script>

<style scoped>
	*{
		font-size:12px;
	}
	.title{
		font-size:20px;
		margin:20px auto;
	}
  
</style>



