<template>
  <div class="err">
    <div class="top">
      <el-row>
        <el-col :span='5'>
          <el-date-picker size='mini' v-model="form.time" type="date" placeholder="选择日期"></el-date-picker>
  		    <!-- <el-date-picker
            v-model="form.value"
            type="daterange"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            default-value="2010-10-01" size="mini">
          </el-date-picker> -->
        </el-col>
        <el-col :span='4'>
          <el-select v-model="form.course" size='mini' placeholder="课程">
            <el-option label="田径运动" value="田径运动"></el-option>
            <el-option label="课间操" value="课间操"></el-option>
          </el-select>  
        </el-col>
        <el-col :span='4'>
  			  <el-select placeholder="年级" size="mini" v-model="form.options">
  			    <el-option
  			      v-for="item in options"
  			      :key="item.value"
  			      :label="item.label"
  			      :value="item.value">
  			    </el-option>
  			  </el-select>
        </el-col>
        <el-col :span='4'>
  			  <el-select placeholder="班级" size="mini" v-model="form.options1">
  			    <el-option
  			      v-for="item in options1"
  			      :key="item.value"
  			      :label="item.label"
  			      :value="item.value">
  			    </el-option>
  			  </el-select>
        </el-col>
        <el-col :span='4'>
  			  <el-select placeholder="体育课：16:00-16:45" size="mini" v-model="form.options3">
  			    <el-option
  			      v-for="item in options3"
  			      :key="item.value"
  			      :label="item.label"
  			      :value="item.value">
  			    </el-option>
  			  </el-select>
        </el-col>
        <el-col :span='3'>
          <el-button type='info' size='mini'>查询</el-button>
        </el-col>
      </el-row>
    </div>
    <div class="content">
      <el-row>	
      	<el-col :span="24">
          <el-row type='flex' justify='center'>
            <span class='title'>异常信息汇总</span>
          </el-row>
        </el-col>
			</el-row>				
      <el-row class='rowErr'>
			  <el-col :span="2" class='rowErr'>异常人数：</el-col>
			  <el-col :span="2" class='rowErr'><div>3</div></el-col>
			  <el-col :span="2" class='rowErr'>异常次数：</el-col>
			  <el-col :span="2" class='rowErr'><div>4</div></el-col>
        <el-col :span="16">
          <el-row type='flex' justify='end'>
            <el-button type="info" size='mini' class='download' @click='download'>下载</el-button>
          </el-row>
        </el-col>
			</el-row>				
    </div>
    <el-table :data="errs" :span-method="rc" border style="width: 100%;" class='tableErr' stripe :header-row-style="rowClass">
      <el-table-column prop="no" label="学号"></el-table-column>
      <el-table-column prop="name" label="学生姓名">
      </el-table-column>
      <el-table-column prop="gender" label="性别">
      </el-table-column>
      <el-table-column prop="errState" label="异常状况"></el-table-column>
      <el-table-column prop="errStart" label="异常开始时间"></el-table-column>
      <el-table-column prop="errSeries" label="异常持续时间"></el-table-column>
      <el-table-column prop="course" label="课程"></el-table-column>
      <el-table-column prop="content" label="课程内容"></el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name:'err',
  data(){
    return {
      form:{},
      errs:[{
        no: '20170101',
        name: '王小',
        gender:'男',
        errState:'接近最高心率',
        errStart:'14:37:35',
        errSeries:'4秒',
        course:'体育课程',
        content:'足球',
      }, {
        no: '20170102',
        name: '小虎',
        gender:'男',
        errState:'接近最高心率',
        errStart:'14:37:35',
        errSeries:'4秒',
        course:'体育课程',
        content:'足球',
      }, {
        no: '20170103',
        name: '王虎',
        gender:'男',
        errState:'接近最高心率',
        errStart:'14:37:35',
        errSeries:'4秒',
        course:'体育课程',
        content:'足球',
      }],
      options: [{
        value: '选项1',
        label: '一年级'
      }, {
        value: '选项2',
        label: '二年级'
      }, {
        value: '选项3',
        label: '三年级'
      }, {
        value: '选项4',
        label: '四年级'
      }, {
        value: '选项5',
        label: '五年级'
      },{
      	value: '选项4',
        label: '全部'
      }],
      options1: [{
        value: '选项1',
        label: '1班'
      }, {
        value: '选项2',
        label: '2班'
      }, {
        value: '选项3',
        label: '3班'
      },{
      	value: '选项4',
        label: '全部'
      }],
      options2: [{
        value: '选项1',
        label: '课间操'
      }, {
        value: '选项2',
        label: '田径运动'
      }, {
        value: '选项3',
        label: '体育'
      },{
      	value: '选项4',
        label: '全部'
      }],
      options3: [{
        value: '选项1',
        label: '课间操：14:00-14:25'
      }, {
        value: '选项2',
        label: '田径运动：15:00-15:45'
      }, {
        value: '选项3',
        label: '体育：16:00-16:45'
      }],
    }
  },
  methods:{
    rc2({row,column,rowIndex,columnIndex}){//合并行、列
      if (rowIndex % 2 === 0) {
        if (columnIndex === 0) {
          return [1,2];
        } else if (columnIndex === 1) {
          return [0,0];
        }
      }
    },
    rc({row,column,rowIndex,columnIndex}){
      if (columnIndex===0||columnIndex===1||columnIndex===2){  //合并第1、2、3列的第一行，从0开始。
        if (rowIndex%2===0) {//第一行
          return [2,1];
        } else {
          return [0,0];
        }
      }
    },
    download(){
      this.$message({
        message:'已成功下载！',
        type:'success',
      });
    },
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{
    font-size:12px;
  }
  .el-row {
  	margin-top: 10px;
    margin-bottom: 0px;
  }
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 5px;
  }
  .title{
    font-size:20px;
  }
  .rowErr{
    height:48px;
    line-height:48px;
  }
  .download{
    margin-bottom:10px;
  }

</style>



