<template>
	<div class="list">
		<el-row>
		  <el-col :span="12">
			 	<template>
				  <el-select v-model="value" placeholder="1号工具箱" size='small'>
				    <el-option
				      v-for="item in options"
				      :key="item.value"
				      :label="item.label"
				      :value="item.value">
				    </el-option>
				  </el-select>
				</template>
		  </el-col>
		  <el-col :span="12">
			 	<el-row type='flex' justify='end'>
          <el-button type="info" @click='single2' size='small'>新增</el-button>
          <el-button type="info" @click='batch2' size='small'>批量导入</el-button>
        </el-row>
		  </el-col>
		</el-row>	 
    <div class='coll2'></div>
    
		<el-table :data="lists" style="width:100%" stripe :header-row-style="rowClass">
			<el-table-column type="selection">
    	</el-table-column>
			<el-table-column type='index' label="序号">
      </el-table-column>
      <el-table-column prop="no" label="手环编号">
      </el-table-column>
      <el-table-column prop="state" label="启用状态">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit2(scope.$index, scope.row)"></i>
          <i class="icon el-icon-delete" @click="delete2(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>
    <div class='coll'></div>
		<el-row>
			<el-col :span="8">
			 	<el-button type="info" size='small'>删除</el-button>
			</el-col>
			<el-col :span="16">
			 	<el-row type='flex' justify='end'>
        	<el-pagination
			      @size-change="handleSizeChange"
			      @current-change="handleCurrentChange"
			      :current-page="currentPage4"
			      :page-sizes="[100, 200, 300, 400]"
			      :page-size="100"
			      layout="total, sizes, prev, pager, next, jumper"
			      :total="400">
			    </el-pagination>
        </el-row>
			</el-col>
		</el-row>
	
		<el-dialog :title="title2" :visible.sync="visible2" center>
      <el-form :model="form2" class='dialog'>
        <el-form-item label="序号" :label-width="fw">
          <el-input v-model="form2.num" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="手环编号" :label-width="fw">
          <el-input v-model="form2.no" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="启用状态" :label-width="fw">
          <el-radio class="radio" v-model="form2.state" label="启用">启用</el-radio>
          <el-radio class="radio" v-model="form2.state" label="停用">停用</el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2">取 消</el-button>
        <el-button type="info" @click="submit2">确 定</el-button>
      </div>
    </el-dialog> 
    <el-dialog :title="title2s" :visible.sync="visible2s" center>
      <el-form :model="form2s" class='dialog3' size='small'>
        <el-form-item label="选择文件" :label-width="fw3">
        	<el-input placeholder="请选择文件" suffix-icon="el-icon-document" v-model="form2s.file"></el-input>
        </el-form-item>
      	<el-form-item label="导入说明" :label-width="fw3">
        	<el-table :data="lists" style="width:100%">
			      <el-table-column prop="name" label="工具箱名称" width="200">
			      </el-table-column>
			      <el-table-column prop="no" label="手环编号" width="120">
			      </el-table-column>
		    </el-table>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2s">取 消</el-button>
        <el-button type="info" @click="submit2s">导 入</el-button>
      </div>
    </el-dialog>
	</div>
</template>

<script>
	export default {
    data() {
      return {
      	fw:'120px',
      	fw3:'170px',
      	currentPage4: 4,
      	value:'',
        title2:'单个新增手环',
      	title2s:'批量新增手环',
        visible2:false,
      	visible2s:false,
        form2:{},
      	form2s:{},
      	lists:[{
      		no:'SH1001',
      		state:'启用',
      	},{
      		no:'SH1002',
      		state:'启用',
      	}],
        options: [{
          value: '1号工具箱',
          label: '1号工具箱',
        }, {
          value: '2号工具箱',
          label: '2号工具箱',
        }],
      }
    },
     methods: {
     	edit2(i,row){this.visible2=true;this.title2='手环信息修改';},
    	delete2(i,row){
        this.$message({
          message: '该条手环信息已成功删除！',
          type: 'success',
        });
      },
    	single2(){this.visible2=true;this.title2='单个新增手环';},
    	batch2(){this.visible2s=true;},
    	submit2(i,row){
        this.$message({
          message: '手环信息提交成功！',
          type: 'success',
        });
        this.visible2=false;
      },
      submit2s(i,row){
        this.$message({
          message: '信息批量导入成功！',
          type: 'success',
        });
        this.visible2s=false;
      },
      cancle2(){this.visible2=false;},
	    cancle2s(){this.visible2s=false;},
      handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        console.log(`当前页: ${val}`);
      },
      rowClass(row, index) {
        return {"background-color":"#A5B0BB",'color':'white'}
      },
    },
  }
</script>

<style scoped>
	.icon {
    cursor: pointer;
    font-size: 20px;
  }
  .coll{
  	height:240px;
  }
  .coll2{
    height:20px;
  }
  .dialog .el-input{
    width:430px;
  }
  .dialog3 .el-input{
    width:300px;
  }
  .dialog-footer{
    text-align:right;
  }
</style>


