<template>
  <div class="addTool">
    <el-row class='row2'>
      <el-col :span='20' align='center'><span class='title'></span></el-col>
      <el-col :span='4'>
        <el-row type='flex' justify='end'>
          <el-button type="info" size='small' @click='add2'>新增</el-button>
        </el-row>
      </el-col>
    </el-row>
    <el-table :data="addTool" height="430" style="width:100%" class='table2' stripe tooltip-effect='blue' ref="multipleTable" :header-row-style="rowClass">
      <el-table-column type='index' label="序号"></el-table-column>
      <el-table-column prop="name" label="工具箱名称"></el-table-column>
      <el-table-column prop="num" label="手环数量"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit2(scope.$index, scope.row)"></i>
          <i class="icon el-icon-delete" @click="delete2(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="title2" :visible.sync="visible2" center>
      <el-form :model="form2" class='dialog'>
        <el-form-item label="序号" :label-width="fw">
          <el-input v-model="form2.no" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="工具箱名称" :label-width="fw">
          <el-input v-model="form2.user" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="手环数量" :label-width="fw">
          <el-input v-model="form2.passwd" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2">取 消</el-button>
        <el-button type="info" @click="submit2">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>

export default {
  name: 'addTool',
  data(){
    return {
      fw:'120px',
      title2:'工具箱',
      visible2:false,
      form2:{},
      addTool: [{
        name: '一号工具箱',
        num: '56',
      },{
        name: '二号工具箱',
        num: '60',
      }],
    }
  },
  methods:{
    edit2(i,row){this.visible2=true;this.title2='工具箱信息修改';},
    delete2(i,row){
      this.$message({
        message: '该条工具箱信息已成功删除！',
        type: 'success',
      });
    },
    submit2(i,row){
      this.$message({
        message: '工具箱信息提交成功！',
        type: 'success',
      });
      this.visible2=false;},
    cancle2(){this.visible2=false;
    },
    add2(){this.visible2=true;this.title2='新增工具箱';},
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .table2{
    margin-top:20px;
    overflow:auto;
  }
  .icon {
    cursor: pointer;
    font-size: 20px;
  }
  .title{
    display:inline-block;
    margin-top:6px;
    font-size:18px;
  }
  .dialog .el-input{
    width:430px;
  }
  .dialog-footer{
    text-align:right;
  }
</style>



