<template>
  <div class="heartRate">
    <el-row>
      <el-col :span='12'>
        <p>基础心率参数设置</p>
      </el-col>
      <el-col :span='12'>
        <!-- <el-row type='flex' justify='end'>
          <el-button type='info' class='but' @click='add2'>编辑</el-button>
        </el-row> -->
      </el-col>
    </el-row>
    <el-table :data="param" style="width: 100%" height="250" size='small' stripe :header-row-style="rowClass">
      <el-table-column prop="name" label="参数名称" width="200">
        <template slot-scope='scope'>
          <el-input v-model='scope.row.name' size='small' class='input'></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="val" label="参数">
        <template slot-scope='scope'>
          <el-input v-model='scope.row.val' size='small' style='width:80%;'></el-input>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit2(scope.$index, scope.row)"></i>
          <i class="icon el-icon-delete" @click="delete2(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>
    <br><br>

    <el-row>
      <el-col :span='12'>
        阶段心率参数设置
      </el-col>
      <el-col :span='12'>
        <el-row type='flex' justify='end'>
          <el-button type='info' class='but' @click='add3'>新增</el-button>
        </el-row>
      </el-col>
    </el-row>
    <el-table :data="param2" style="width: 100%" height="250" size='small' stripe :header-row-style="rowClass">
      <el-table-column prop="name" label="参数名称" width="200">
        <template slot-scope='scope'>
          <el-input v-model='scope.row.name' size='small' class='input'></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="val" label="参数">
        <template slot-scope='scope'>
          <el-input v-model='scope.row.val' size='small' style='width:80%;'></el-input>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit3(scope.$index, scope.row)"></i>
          <i class="icon el-icon-delete" @click="delete3(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="title2" :visible.sync="visible2">
      <el-form :model="form2">
        <el-form-item label="参数">
          <el-input v-model="form2.val" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
         <el-button @click="cancle2">取 消</el-button>
         <el-button type="info" @click="submit2">确 定</el-button>
      </div>
    </el-dialog>
    <el-dialog :title="title3" :visible.sync="visible3">
      <el-form :model="form3">
        <el-form-item label="参数">
          <el-input v-model="form3.val" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
         <el-button @click="cancle3">取 消</el-button>
         <el-button type="info" @click="submit3">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>

export default {
  name: 'heartRate',
  data(){
    return {
      title2:'编辑参数',
      title3:'新增参数',
      visible2:false,
      visible3:false,
      form2:{},
      form3:{},
      param:[{
        name:'最高心率',
      },{
        name:'最低心率',
      },{
        name:'平均心率',
      }],
      param2:[{
        name:'阶段一心率',
      },{
        name:'阶段二心率',
      },{
        name:'阶段三心率',
      }],
    }
  },
  methods:{
    add2(){
      this.visible2=true;
    },
    add3(){
      this.visible3=true;
    },
    edit2(){
      this.visible2=true;
    },
    edit3(){
      this.visible3=true;
    },
    cancle2(){this.visible2=false;},
    cancle3(){this.visible3=false;},
    submit2(){
      this.$message({
        message: '基础心率信息设置成功！',
        type: 'success',
      });
      this.visible2=false;
    },
    submit3(){
      this.$message({
        message: '阶段心率信息设置成功！',
        type: 'success',
      });
      this.visible3=false;
    },
    delete2(i,row){
      this.$message({
        message: '基础心率信息已成功删除！',
        type: 'success',
      });
    },
    delete3(i,row){
      this.$message({
        message: '阶段心率信息已成功删除！',
        type: 'success',
      });
    },
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .icon {
    cursor: pointer;
    font-size: 20px;
  }
  .input{
    display:inline-block;
    width:120px;
    height:36px;
    line-height:36px;
    text-align:center;
  }
  
</style>



