<template>
  <div class="remark">
    <el-row class='row2'>
      <el-col :span='20' align='center'><span class='title'>备注学生上课内容</span></el-col>
      <el-col :span='4'><el-button type="info" size='small' @click='save'>保存</el-button></el-col>
    </el-row>
    <el-table :data="remark" height="466" size='mini' style="width:100%" class='table2' stripe :header-row-style="rowClass">
      <el-table-column prop="no" label="学号" style="width:25%"></el-table-column>
      <el-table-column prop="name" label="学生姓名" style="width:25%"></el-table-column>
      <el-table-column label="上课内容" style="width:25%">
        <template slot-scope='s'>
          <el-select v-model="s.row.content" style="width:60%">
            <el-option label="足球" value="足球"></el-option>
            <el-option label="篮球" value="篮球"></el-option>
            <el-option label="羽毛球" value="羽毛球"></el-option>
          </el-select>
        </template>
      </el-table-column>
      <!-- <el-table-column label="操作" style="width:25%">
        <template slot-scope="scope">
          <i class="icon el-icon-edit" @click="edit2(scope.$index, scope.row)"></i>
          <i class="icon el-icon-delete" @click="delete2(scope.$index, scope.row)"></i>
        </template>
      </el-table-column> -->
    </el-table>
  </div>
</template>

<script>

export default {
  name: 'remark',
  data(){
    return {
     remark: [{
        no: '20170101',
        name: '张三',
        content: '羽毛球'
      }, {
        no: '20170101',
        name: '张三',
        content: '篮球'
      }, {
        no: '20170101',
        name: '张三',
        content: '足球'
      }],
    }
  },
  methods:{
    save(){
      this.$message({
        message: '保存成功！',
        type: 'success',
      });
    },
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{
    font-size:12px;
  }
  .row2{
    margin-top:20px;
  }
  .table2{
    margin-top:20px;
    overflow:auto;
  }
  .icon {
    cursor: pointer;
    font-size: 20px;
  }
  .title{
    display:inline-block;
    margin-top:6px;
    font-size:18px;
  }
</style>



