	              <el-option label="一班" value="一班"></el-option>
	              <el-option label="二班" value="二班"></el-option>
	            </el-select>
            </el-form-item>
        	</el-form>
        </el-row>
		  </el-col>
		  <el-col :span="5">
		  	<el-row>
			  	<el-form inline class='form'>
			  		<el-form-item>
	            <el-select v-model="form.course" size='mini' placeholder="当前课程">
	              <el-option label="田径运动" value="田径运动"></el-option>
	              <el-option label="课间操" value="课间操"></el-option>
	            </el-select>
	          </el-form-item>
	        </el-form>
        </el-row>
		  </el-col>
		</el-row>
    <!--<el-row>
      <el-col :span='19'>
        <el-form inline class='form'>
          <el-form-item label='上课班级'>
            <el-select v-model="form.grade" size='mini' placeholder="年级">
              <el-option label="一年级" value="一年级"></el-option>
              <el-option label="二年级" value="二年级"></el-option>
            </el-select>
            <el-select v-model="form.clazz" size='mini' placeholder="班级">
              <el-option label="一班" value="一班"></el-option>
