    <el-row>
      <el-col :span='19'>
        <el-row>
          <el-col :span='10'>
            <el-row>
              <el-col :span='8' class='font'>班级最高心率：<a>138</a></el-col>
              <el-col :span='8' class='font'>班级最低心率：<a>75</a></el-col>
              <el-col :span='8' class='font'>班级平均心率：<a>95</a></el-col>
            </el-row>
          </el-col>
          <el-col :span='3' class='font'>阶段一心率：<a>95</a></el-col>
          <el-col :span='3' class='font'>阶段二心率：<a>105</a></el-col>
          <el-col :span='3' class='font'>阶段三心率：<a>115</a></el-col>
          <el-col :span='5'>
            <el-row type='flex' justify='end'>
              <el-select v-model="form.rate" size='mini' placeholder="班级心率曲线">
                <el-option label="班级心率曲线" value="班级心率曲线"></el-option>
                <el-option label="班级心率列表" value="班级心率列表"></el-option>
              </el-select>
            </el-row>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span='5'></el-col>
    </el-row>
    <el-row type='flex' justify='center'>
      <span class='title'>异常信息汇总</span>
    </el-row>
    <el-row>
      <el-col :span='12'>
        <span>异常人数：<a>3</a></span>
        <span>异常次数：<a>4</a></span>
      </el-col>
      <el-col :span='12'>
        <el-row type='flex' justify='end'>
          <el-button size='mini' @click='download'>下载</el-button>
          <el-button size='mini' @click='back'>返回</el-button>
        </el-row>
      </el-col>
    </el-row>
    <el-table :data="errs" :span-method="rc" style="width: 100%;" class='table2' height='410' stripe :header-row-style="rowClass">
      <el-table-column prop="no" label="学号" width="180"></el-table-column>
      <el-table-column prop="name" label="学生姓名">
      </el-table-column>
      <el-table-column prop="gender" label="性别">
      </el-table-column>
      <el-table-column prop="errState" label="异常状况"></el-table-column>
      <el-table-column prop="errStart" label="异常开始时间"></el-table-column>
      <el-table-column prop="errSeries" label="异常持续时间"></el-table-column>
