<script>

export default {
  name: 'err',
  data(){
    return {
      form:{},
      errs:[{
        no: '20170101',
        name: '王小',
        gender:'男',
        errState:'接近最高心率',
        errStart:'14:37:35',
        errSeries:'4秒',
        course:'体育课程',
        content:'足球',
      }, {
        no: '20170102',
        name: '小虎',
        gender:'男',
        errState:'接近最高心率',
        errStart:'14:37:35',
        errSeries:'4秒',
        course:'体育课程',
        content:'足球',
      }, {
        no: '20170103',
        name: '王虎',
        gender:'男',
        errState:'接近最高心率',
        errStart:'14:37:35',
        errSeries:'4秒',
        course:'体育课程',
        content:'足球',
      }],
    }
  },
  methods:{
    rc2({row,column,rowIndex,columnIndex}){//合并行、列
      if (rowIndex % 2 === 0) {
        if (columnIndex === 0) {
          return [1,2];
        } else if (columnIndex === 1) {
          return [0,0];
        }
      }
    },
    rc({row,column,rowIndex,columnIndex}){
      if (columnIndex===0||columnIndex===1||columnIndex===2){  //合并第1、2、3列的第一行，从0开始。
        if (rowIndex%2===0) {//第一行
          return [2,1];
        } else {
          return [0,0];
        }
      }
    },
    download(){
      this.$message({
        message: '下载成功！',
        type: 'success',
      });
    },
    back(){
      this.$message({
        message: '返回失败！',
        type: 'error',
      });
    },
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{
    font-size:12px;
  }
  .form{
    height:38px;
  }
  .font{
    display:inline-block;
    line-height:30px;
  }
  .td{
    height:80px;
    border:1px solid black;
    margin-left:10px;
    text-align:center;
    padding:10px;
    margin:5px;
  }
  .content{
    border:1px solid #999;
    box-shadow:6px 6px 12px #999;
    padding:10px 0;
    height:480px;
    overflow:auto;
  }
  .errStu{
    height:230px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
    margin-bottom:20px;
  }
  .getInfo{
    height:200px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
  }
  .query:hover{
    text-decoration:underline;
    color:red;
    cursor:pointer;
  }
  .title{
    margin-top:20px;
    font-size:20px;
  }
  .table2{
    overflow:auto;
  }

</style>



