<template>
	<el-row class="unit" :data='unit'>
  	<el-col :span="12" class="tab">
	  	<el-row class='row'>
			  <el-col :span="8"><div class="grid-content bg-purple">学校名称：</div></el-col>
			  <el-col :span="16"><div class="grid-content bg-purple"><input type="text" v-model='unit.name'></div></el-col>
			</el-row>
			<el-row class='row'>
			  <el-col :span="8"><div class="grid-content bg-purple">学校地址：</div></el-col>
			  <el-col :span="16"><div class="grid-content bg-purple"><input type="text" v-model='unit.address'></div></el-col>
			</el-row>
			<el-row class='row'>
			  <el-col :span="8"><div class="grid-content bg-purple">联系电话：</div></el-col>
			  <el-col :span="16"><div class="grid-content bg-purple"><input type="text" v-model='unit.type'></div></el-col>		
			</el-row>
			<el-row class='row'>
			  <el-col :span="8"><div class="grid-content bg-purple">学校类型：</div></el-col>
			  <el-col :span="16"><div class="grid-content bg-purple"><input type="text" v-model='unit.type'></div></el-col>
			</el-row>
			<el-row class='row'>
			  <el-col :span="8"><div class="grid-content bg-purple">当前学年：</div></el-col>
			  <el-col :span="16"><div class="grid-content bg-purple">
			  	<select v-model='unit.year'>
	          <option value="2017-2018">2017-2018</option>
	          <option value="2016-2017">2016-2017</option>
        	</select></div>
			  </el-col>
			</el-row>
			<el-row class='row'>
			  <el-col :span="8"><div class="grid-content bg-purple">当前学期：</div></el-col>
			  <el-col :span="16"><div class="grid-content bg-purple">
			  	<select v-model='unit.term'>
		          <option value="第一学期">第一学期</option>
		          <option value="第二学期">第二学期</option>
		        </select></div>
			  </el-col>
			</el-row>
			<el-button type='info' class='emit' @click='edit'>修改</el-button>
		</el-col>
		<el-col :span="12">
      <el-row type='flex' justify='center'>
        <el-col :span='16'>
          <el-row>
            <div class="logo">logo</div>
          </el-row>
          <el-row>
            <el-form size='small'>
              <el-row>
                <el-col :span='19'>
                  <el-form-item>
                    <el-input v-model="input" placeholder="请输入内容" ></el-input>
                  </el-form-item>
                </el-col>
                <el-col :span='5'>
                  <el-form-item>
                    <el-button type="primary" @click='up'>上传<i class="el-icon-upload el-icon-right"></i></el-button>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
          </el-row>
        </el-col>
			</el-row>
		</el-col>
	</el-row>
</template>

<script type="text/javascript">
export default {
  name: 'unitInfo',
  data(){
    return {
    	input: '',
      unit:{
        name : '福山路小学',
        address : '上海市浦东新区福山路1号',
        phone : '021-123456789',
        type : '九年一贯制',
        year : '2016-2017',
        term : '第二学期',
      },
  	}
  },
  methods:{
    edit(){
      this.$message({
        message: '单位信息修改成功！',
        type: 'success',
      });
    },
    up(){
      this.$message({
        message: 'logo上传成功，系统logo已切换！',
        type: 'success',
      });
    },
  },
}
</script>

<style type="text/css" scoped>
	.unit{
    width:100%;
    height:450px;
    padding: 40px;
    margin-top: 20px;
    border:1px solid #CCCCCC;
  }
  .row {
    border-bottom:0px solid #CCCCCC; 
    border:1px solid #CCCCCC;       
  }
  .row .el-col {
    text-align: center;
    line-height:30px;
  }
  input{
  	width: 99%;
  	height: 28px;
    border:none;
  }
  select{
  	width: 100%;
  	height: 33px;
    border:none;
  }
  .logo{
    width:100%;
  	height:200px;
  	line-height:200px;
  	border:1px solid #ccc;
    text-align:center;
  }
  .emit{
  	margin-top: 50px;
  }
</style>


