<!-- 教师用户  -->
<template>
	<div class="teacher">
		<el-row>
			<el-col :span='14'>
				<el-form inline>
          <el-row>
            <el-col :span='6'>
              <el-form-item>
                <el-input v-model='keys' size='mini' placeholder='请输入关键字'></el-input>
              </el-form-item>
            </el-col>
            <el-col :span='6'>
              <el-form-item>
                <el-button type='info' size='mini'>查询</el-button>
              </el-form-item>
            </el-col>
        </el-row>
				</el-form>
			</el-col>
			<el-col :span='10'>
				<el-row type='flex' justify='end'>
          <el-form inline>
            <el-row>
              <el-col :span='8'>
                <el-form-item>
                  <el-button type='info' size='mini' @click='single2'>新增教师</el-button>
                </el-form-item>
              </el-col>
              <el-col :span='8'>
                <el-form-item>
                  <el-button type='info' size='mini' @click='admin2'>用户授权</el-button>
                </el-form-item>
              </el-col>
              <el-col :span='8'>
                <el-form-item>
                  <el-button type='info' size='mini' @click='batch2'>批量导入</el-button>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
				</el-row>
			</el-col>
		</el-row>
    <el-table :data="teacher" style="width: 100%" height="426" class='table' :header-row-style="rowClass">
      <el-table-column type="selection"></el-table-column>
      <el-table-column type="index" label='序号'></el-table-column>
      <el-table-column prop="name" label="姓名">
      </el-table-column>
      <el-table-column prop="user" label="教师帐号">
      </el-table-column>
      <el-table-column prop="phone" label="手机号码">
      </el-table-column>
      <el-table-column prop="gender" label="性别">
      </el-table-column>
      <el-table-column prop="state" label="启用状态">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit2(scope.$index, scope.row)"></i>
	        <i class="icon el-icon-delete" @click="delete2(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="title2" :visible.sync="visible2" center>
      <el-form :model="form2" class='dialog'>
          
        <el-form-item label="序号" :label-width="fw">
          <el-input v-model="form2.no" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="教师帐号" :label-width="fw">
          <el-input v-model="form2.user" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="登录密码" :label-width="fw">
          <el-input v-model="form2.passwd" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="手机号码" :label-width="fw">
          <el-input v-model="form2.phone" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="性别" :label-width="fw">
          <el-radio class="radio" v-model="form2.gender" label="男">男</el-radio>
          <el-radio class="radio" v-model="form2.gender" label="女">女</el-radio>
        </el-form-item>
        <el-form-item label="启用状态" :label-width="fw">
          <el-radio class="radio" v-model="form2.state" label="启用">启用</el-radio>
          <el-radio class="radio" v-model="form2.state" label="停用">停用</el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2">取 消</el-button>
        <el-button type="info" @click="submit2">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog :title="title2s" :visible.sync="visible2s" center>
      <el-form :model="form2s" class='dialog2' size='small'>
        <el-form-item label="下载导入模版" :label-width="fw">
          <el-input placeholder="请选择文件" suffix-icon="el-icon-document" v-model="form2s.file"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle2s">取 消</el-button>
        <el-button type="info" @click="submit2s">导 入</el-button>
      </div>
    </el-dialog> 

    <el-dialog :title="titleAdmin" :visible.sync="visibleAdmin" center>
      <el-form :model="formAdmin">
        <a></a><el-checkbox v-model="formAdmin.survey">实时监测</el-checkbox><hr color='#eee'>
        <a></a><el-checkbox v-model="formAdmin.data">数据报表</el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.curve">班级曲线报表</el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.clazzData">班级数据报表</el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.err">异常报表</el-checkbox><hr color='#eee'>
        <a></a><el-checkbox v-model="formAdmin.set">平台设置</el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.unit">单位信息</el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.user">用户管理</el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.table">课表管理</el-checkbox><hr color='#eee'>
        <b></b><el-checkbox v-model="formAdmin.rate">默认心率设置</el-checkbox><hr color='#eee'>
      </el-form>
      <div slot="footer" class="dialog-footer">
         <el-button @click="cancleAdmin">取 消</el-button>
         <el-button type="info" @click="submitAdmin">确 定</el-button>
      </div>
    </el-dialog>  
	</div>
</template>

<script>
import {mapActions,mapGetters} from 'vuex';

export default {
  name: 'teacher',
  data(){
    return {
      fw:'120px',
    	keys:'',
      title2:'教师用户',
      title2s:'批量添加教师用户',
      titleAdmin:'用户授权',
      form2:{},
      form2s:{},
      formAdmin:{},
      visible2:false,
      visible2s:false,
      visibleAdmin:false,
    }
  },
  created(){
    this.findAllTch();
  },
  computed:{
    ...mapGetters(['teacher']),
  },
  methods:{
    edit2(i,row){
      this.visible2=true;
      this.title2='教师信息修改';
      this.form2 = row;
    },
    delete2(i,row){
      var id = row.id;
      this.deleteByIdTch(id).then((result)=>{
        this.$message({
          type: 'success',
          message: '教师信息删除成功',
        });
      }).catch((error)=>{
        this.$message({
          type: 'error',
          message: '教师信息删除失败',
        });
      });
    },
    single2(){
      this.visible2=true;
      this.title2='单个新增教师';
      this.form2 = row;
    },
    batch2(){this.visible2s=true;},
    admin2(){this.visibleAdmin=true;this.titleAdmin='用户授权';},
    submit2(){
      this.saveTch(this.form2).then((result)=>{
        this.$message({
          type: 'success',
          message: '操作成功'
        });
      }).catch((error)=>{
        this.$message({
          type: 'error',
          message: '操作失败'
        });
      });
      this.visible2=false;
    },
    submit2s(){
      this.saveTch(this.form2).then((result)=>{
        this.$message({
          type: 'success',
          message: '操作成功'
        });
      }).catch((error)=>{
        this.$message({
          type: 'error',
          message: '操作失败'
        });
      });
      this.visible2s=false;
    },
    submitAdmin(i,row){
      this.saveTch(this.formAdmin).then((result)=>{
        this.$message({
          type: 'success',
          message: '操作成功'
        });
      }).catch((error)=>{
        this.$message({
          type: 'error',
          message: '操作失败'
        });
      });
      this.visibleAdmin=false;
    },
    cancle2(){this.visible2=false;},
    cancle2s(){this.visible2s=false;},
    cancleAdmin(){this.visibleAdmin=false;},
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
    ...mapActions(['findAllTch','saveTch','deleteByIdTch']),
  }
}
</script>

<style scoped>
  .icon {
		cursor: pointer;
		font-size: 20px;
	}
  .table{
    overflow:auto;
  }
  .dialog .el-input{
    width:86%;
  }
  .dialog-footer{
    text-align:right;
  }
  a{
    display:inline-block;
    width:50px;
  }
  b{
    display:inline-block;
    width:100px;
  }
</style>


