<!-- 学生用户 -->
<template>
	<div class="student">
		<el-row>
			<el-col :span='16'>
				<el-form inline :model='stu'>
          <el-row>
            <el-col :span='6'>
              <el-form-item>
                <el-select v-model="stu.grade" placeholder="请选择年级" size='mini'>
                  <el-option label="小学一年级" value="小学一年级"></el-option>
                  <el-option label="小学二年级" value="小学二年级"></el-option>
                  <el-option label="小学三年级" value="小学三年级"></el-option>
                  <el-option label="小学四年级" value="小学四年级"></el-option>
                  <el-option label="小学五年级" value="小学五年级"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span='6'>
              <el-form-item>
                <el-select v-model="stu.clazz" placeholder="请选择课程内容" size='mini'>
                  <el-option label="一班" value="一班"></el-option>
                  <el-option label="二班" value="二班"></el-option>
                  <el-option label="三班" value="三班"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span='6'>
              <el-form-item>
                <el-input v-model='stu.keys' size='mini' placeholder='请输入关键字'></el-input>
              </el-form-item>
            </el-col>
            <el-col :span='6'>
              <el-form-item>
                <el-button type='info' size='mini'>查询</el-button>
              </el-form-item>
            </el-col>
          </el-row>
				</el-form>
			</el-col>
			<el-col :span='8'>
				<el-row type='flex' justify='end'>
          <el-form inline>
            <el-row>
              <el-col :span='12'>
                <el-form-item>
                  <el-button type='info' size='mini' @click='single3'>新增学生</el-button>
                </el-form-item>
              </el-col>
              <el-col :span='12'>
                <el-form-item>
                  <el-button type='info' size='mini' @click='batch3'>批量导入</el-button>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form>
				</el-row>
			</el-col>
		</el-row>
    <el-table :data="student" style="width:100%" height="426" class='table' :header-row-style="rowClass">
      <!-- <el-table-column prop="no" label="序号" width="100">
      </el-table-column> -->
      <el-table-column prop="stu_account" label="帐号">
      </el-table-column>
      <el-table-column prop="stu_name" label="姓名">
      </el-table-column>
      <el-table-column prop="stu_number" label="学号">
      </el-table-column>
      <el-table-column prop="stu_gender" label="性别">
      </el-table-column>
      <!-- <el-table-column prop="num" label="手环编号">
        </el-table-column>     -->  
      <el-table-column prop="min_heart" label="最低心率">
      </el-table-column>
      <el-table-column prop="avg_heart" label="平均心率">
      </el-table-column>
      <el-table-column prop="max_heart" label="最高心率">
      </el-table-column>
      <el-table-column prop="first_heart" label="阶段一心率">
      </el-table-column>
      <el-table-column prop="second_heart" label="阶段二心率">
      </el-table-column>
      <el-table-column prop="third_heart" label="阶段三心率">
      </el-table-column>
      <el-table-column prop="stu_height" label="身高">
      </el-table-column>
      <el-table-column prop="stu_weight" label="体重">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit3(scope.$index, scope.row)"></i>
	        <i class="icon el-icon-delete" @click="delete3(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="title3" :visible.sync="visible3" center>
      <el-form :model="form3" class='dialog' size='small'>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="序号" :label-width="fw">
              <el-input v-model="form3.no" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="帐号" :label-width="fw">
              <el-input v-model="form3.stu_account" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="学号" :label-width="fw">
              <el-input v-model="form3.stu_number" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="姓名" :label-width="fw">
              <el-input v-model="form3.stu_name" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="年级" :label-width="fw">
              <el-select v-model="form3.stu_grade" placeholder="请选择年级">
                <el-option label="一年级" value="一年级"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="班级" :label-width="fw">
              <el-select v-model="form3.stu_clazz" placeholder="请选择班级">
                <el-option label="一班" value="一班"></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="手环编号" :label-width="fw">
              <el-input v-model="form3.stu_num" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="平均心率" :label-width="fw">
              <el-input v-model="form3.avg_heart" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="最低心率" :label-width="fw">
              <el-input v-model="form3.min_heart" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="最高心率" :label-width="fw">
              <el-input v-model="form3.max_height" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="阶段一心率" :label-width="fw">
              <el-input v-model="form3.first_heart" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="阶段二心率" :label-width="fw">
              <el-input v-model="form3.second_heart" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="阶段三心率" :label-width="fw">
              <el-input v-model="form3.third_heart" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="身高" :label-width="fw">
              <el-input v-model="form3.stu_height" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="体重" :label-width="fw">
              <el-input v-model="form3.stu_weight" auto-complete="off"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span='12'>
            <el-form-item label="性别" :label-width="fw">
              <el-radio class="radio" v-model="form3.stu_gender" label="男">男</el-radio>
              <el-radio class="radio" v-model="form3.stu_gender" label="女">女</el-radio>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span='12'>
            <el-form-item label="启用状态" :label-width="fw">
              <el-radio class="radio" v-model="form3.stu_state" label="启用">启用</el-radio>
              <el-radio class="radio" v-model="form3.stu_state" label="停用">停用</el-radio>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle3">取 消</el-button>
        <el-button type="info" @click="submit3">确 定</el-button>
      </div>
    </el-dialog>

    <el-dialog :title="title3s" :visible.sync="visible3s" center>
      <el-form :model="form3s" class='dialog3' size='small'>
        <el-form-item label="下载导入模版" :label-width="fw">
          <el-input placeholder="请选择文件" suffix-icon="el-icon-document" v-model="form3s.file"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle3s">取 消</el-button>
        <el-button type="info" @click="submit3s">导 入</el-button>
      </div>
    </el-dialog> 
	</div>
</template>

<script>
import {mapActions,mapGetters} from 'vuex';

export default {
  name: 'student',
  data(){
    return {
      fw:'100px',
    	stu:{},
      title3:'学生用户',
      title3s:'批量导入',
      form3:{},
      form3s:{},
      visible3:false,
      visible3s:false,
    }
  },
  created(){
    this.findAllStu();
  },
  computed:{
    ...mapGetters(['student']),
  },
  methods:{
    edit3(i,row){
      this.visible3=true;
      this.title3='学生信息修改';
      this.form3 = row;
    },
    delete3(i,row){
      var id = row.id;
      this.deleteByIdStu(id).then((result)=>{
        this.$message({
          type: 'success',
          message: '删除成功'
        });
      }).catch((error)=>{
        this.$message({
          type: 'error',
          message: '删除失败'
        });
      });
    },
    single3(){
      this.visible3=true;
      this.title3='单个新增学生';
      this.form3 = {stu_gender:'女'};
    },
    batch3(){
      this.visible3s=true;
    },
    submit3(){
      this.saveStu(this.form3).then((result)=>{
        this.$message({
          type: 'success',
          message: '操作成功'
        });
      }).catch((error)=>{
        console.log(error);
        this.$message({
          type: 'error',
          message: '操作失败'
        });
      });
      this.visible3=false;
    },
    submit3s(){
      this.saveStu(this.form3s).then((result)=>{
        this.$message({
          type: 'success',
          message: '操作成功'
        });
      }).catch((error)=>{
        this.$message({
          type: 'error',
          message: '操作失败'
        });
      });
      this.visible3s=false;
    },
    cancle3(){this.visible3=false;},
    cancle3s(){this.visible3s=false;},
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
    ...mapActions(['findAllStu','saveStu','deleteByIdStu']),
  },
}
</script>

<style scoped>
  .icon {
		cursor: pointer;
		font-size: 20px;
	}
  .table{
    overflow:auto;
  }
  .dialog-footer{
    text-align:right;
  }
</style>


