import axios from 'axios';
var bp = 'http://127.0.0.1:3000';

export default {
	state:{
		teacher:[],
	},
	getters:{
		teacher:state => state.teacher,
	},
	mutations:{
		allTch:(state,val) => {
			state.teacher = val;
		}
	},
	actions:{
		saveTch:(context,form) => {
			return new Promise((resolve,reject)=>{
			//add:
				if(form.id==undefined){//如果id不存在，执行添加。
					axios.post(bp+'/teacher/save',form).then((result)=>{
						context.dispatch('findAllTch');//刷新。
						resolve(result);
					}).catch((error)=>{
						reject(error);
					});
			//update:
				}else{  //否则执行修改。
					axios.post(bp+'/teacher/update',form).then((result)=>{
						context.dispatch('findAllTch');//刷新。
						resolve(result);
					}).catch((error)=>{
						reject(error);
					});
				}
			});
		},
		//find:
		findAllTch:(context) => {
			axios.post(bp+'/teacher/findAllTch').then(({data})=>{
				context.commit('allTch',data);
			});
		},
		//delete:
		deleteByIdTch:(context,id) => {
			return new Promise((resolve,reject)=>{
				axios.post(bp+'/teacher/deleteById',{id:id}).then((result)=>{
					context.dispatch('findAllTch');//刷新。
					resolve(result);
				}).catch((error)=>{
					reject(error);
				});
			});
		},
	},
}



