              <el-col :span='8'><a class='query' @click='more'>查看更多</a></el-col>
            </el-row>
            <el-row class='errStu'>
              <el-col :span='24'>
                <el-row class='height'>
                  <el-col :span='6'>孙翔</el-col>
                  <el-col :span='6'>138</el-col>
                  <el-col :span='12'>超过最高心率</el-col>
                </el-row>
                <el-row class='height'>
