<script>
let echarts = require('echarts');
require('echarts/lib/component/tooltip');

export default {
  name: 'clazzRate',
  data(){
    return {
      form:{},
    }
  },
  methods:{
    more(){
      this.$message({
        message: '查看成功！',
        type: 'success',
      });
    },
  },
  mounted(){
    var colors = ['#3300ff','#00ff99','#ffcc00'];
    var myChart = echarts.init(document.getElementById('main'));
    var arr = [],arr1 = [],arr2 = [],arr3 = [];
    var arrx = [],arry1 = [],arry2 = [],arry3 = [];
    var start = 0,end = 31;
    for(var k=0;k<=602;k=k+2){
      arr.push(k);
    }
    for(var k=0;k<301;k++){
      var random1 = Math.floor(Math.random()*199+1);
      var random2 = Math.floor(Math.random()*199+1);
      var random3 = Math.floor(Math.random()*199+1);
      arr1.push(random1);
      arr2.push(random2);
      arr3.push(random3);
    }
    function sw(){
      arrx = [],arry1 = [],arry2 = [],arry3 = [];
      for(var i=start;i<end;i++){
        arrx.push(arr[i]);
        arry1.push(arr1[i]);
        arry2.push(arr2[i]);
        arry3.push(arr3[i]);
      }
    }
    function time(){//获取当前时间。
      var date = new Date();
      date = date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()+" &nbsp; "+repair(date.getHours())+':'+repair(date.getMinutes());
      return date;
    }
    function repair(n){//时间补0函数
      n = n + '';
      var str = '00';
      return str.slice(0,2-n.length)+n;
    }
    // 绘制图表
    function curve(){
      if(end<arr.length){
        sw();
        start++;
        end++;
        var option = {
          color: colors,
          title:{
            text:'班级心率曲线',
            left:'center',
            top:'10',
            subtext:'四年级  1班   体育课程',
            subtextStyle:{
              color:'#aaa',
              fontWeight:'bolder',
              align:'left'
            },
          },
          tooltip: {
            show:true,
            trigger:'axis',
            axisPointer:'shadow',
            snap:'snap',
            formatter: function(params){
              var date = time();
              var str = date + '<br/>' + '班级平均心率：' + params[0].value + '次/秒<br/>' + '男生平均心率：' + params[1].value + '次/秒<br/>' + '女生平均心率：' + params[2].value + '次/秒';
              return str;
            },
            textStyle:{//文本样式。
              color:['white'],
            },
          },
          legend: {
            data:['班级平均心率','男生平均心率','女生平均心率'],
            x:'right',
            top:35,
          },
          calculable : true,
          grid: {  //图表的内外边距
            top: 120,
            bottom: 50,
          },
          xAxis: [{  //X轴坐标参数
            name:'时间/秒',
            type: 'category',
            boundaryGap : false, //分界线范围
            data: arrx,
          }],
          yAxis: [{
            name:'心率/次数',
            type: 'value',
          }],
          series: [{
            name:'班级平均心率',
            type:'line',
            data: arry1,
            markPoint : {
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type :'average', name:'平均值'}
              ]
            },
          },{    
            name:'男生平均心率',
            type:'line',
            data: arry2,
            markPoint : {
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type :'average', name:'平均值'}
              ]
            },
          },{
            name:'女生平均心率',
            type:'line',
            data: arry3,
            markPoint : {
              data : [
                {type : 'max', name: '最大值'},
                {type : 'min', name: '最小值'}
              ]
            },
            markLine : {
              data : [
                {type :'average', name:'平均值'}
              ]
            },
          }],
        }
        myChart.setOption(option);
      }
    }
    setInterval(function(){
      curve();
    },2000);
    curve();
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{
    font-size:12px;
  }
  .form{
    height:38px;
  }
  .font{
    display:inline-block;
    line-height:30px;
  }
  .height{
    height:30px;
    line-height:30px;
    margin-left:10px;
  }
  .td{
    height:80px;
    border:1px solid black;
    margin-left:10px;
    text-align:center;
    padding:10px;
    margin:5px;
  }
  .content{
    margin-top:10px;
    border:1px solid #999;
    box-shadow:6px 6px 12px #999;
    padding:10px 0;
    height:450px;
    overflow:auto;
  }
  .errStu{
    height:230px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
    margin-bottom:20px;
    margin-top:5px;
  }
  .getInfo{
    height:200px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
    margin-top:5px;
  }
  .query:hover{
    text-decoration:underline;
    color:red;
    cursor:pointer;
  }
	.el-select{
    width:100%;
  }
</style>



