import axios from 'axios';
var bp = 'http://10.0.7.46:8080/project/bracelet/Home';

export default {
	state:{
		student:[],
	},
	getters:{
		student:state => state.student,
	},
	mutations:{
		allStu:(state,val) => {
			state.student = val;
		}
	},
	actions:{
		saveStu:(context,form) => {
			return new Promise((resolve,reject)=>{
			//add:
				if(form.id==undefined){//如果id不存在，执行添加。
					var f = JSON.stringify({"data":Array(form)});
					axios.post(bp+'/manager/student',f).then((result)=>{
						context.dispatch('findAllStu');//刷新。
						console.log(result)
					//	var array=eval("("+result.config.data+")"); 
					//	console.log(array.data);
						resolve(result);
					}).catch((error)=>{
						reject(error);
					});
			//update:
				}else{  //否则执行修改。
					axios.post(bp+'/manager/update',form).then((result)=>{
						context.dispatch('findAllStu');//刷新。
						resolve(result);
					}).catch((error)=>{
						reject(error);
					});
				}
			});
		},
		//find:
		findAllStu:(context) => {
			axios.post(bp+'/manager/student').then(({data})=>{
				context.commit('allStu',data);
			});
		},
		//delete:
		deleteByIdStu:(context,id) => {
			return new Promise((resolve,reject)=>{
				axios.post(bp+'/manager/delete_student',{id:id}).then((result)=>{
					context.dispatch('findAllStu');//刷新。
					resolve(result);
				}).catch((error)=>{
					reject(error);
				});
			});
		},
	}
}


