<!-- 年级设置  -->
<template>
	<div class="gradeSetting">
		<el-row type='flex' justify='end'>
			<el-button type='info' size='small' @click='add3'>新增年级</el-button>
		</el-row>
    <el-table :data="gradeData" style="width: 100%" height="450" class='table' stripe :header-row-style="rowClass">
      <el-table-column type="index"></el-table-column>
      <el-table-column prop="name" label="年级名称">
      </el-table-column>
      <el-table-column prop="sName" label="年级简称">
      </el-table-column>
       <el-table-column prop="state" label="状态">
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit3(scope.$index, scope.row)"></i>
	        <i class="icon el-icon-delete" @click="delete3(scope.$index, scope.row)"></i>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog :title="title3" :visible.sync="visible3" center>
      <el-form :model="form3" class='dialog'>
        <el-form-item label="序号" :label-width="fw">
          <el-input v-model="form3.no" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="年级名称" :label-width="fw">
          <el-input v-model="form3.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="年级简称" :label-width="fw">
          <el-input v-model="form3.sName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="是否毕业年级" :label-width="fw">
          <el-radio class="radio" v-model="form3.yes" label="是">是</el-radio>
          <el-radio class="radio" v-model="form3.yes" label="否">否</el-radio>
        </el-form-item>
        <el-form-item label="启用状态" :label-width="fw">
          <el-radio class="radio" v-model="form3.state" label="启用">启用</el-radio>
          <el-radio class="radio" v-model="form3.state" label="停用">停用</el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancle3">取 消</el-button>
        <el-button type="info" @click="submit3">确 定</el-button>
      </div>
    </el-dialog>
	</div>
</template>

<script type="text/javascript">
export default {
  name: 'gradeSetting',
  data(){
    return {
      fw:'120px',
      title3:'年级',
      form3:{},
      visible3:false,
      gradeData: [{
        name:'小学一年级',
        sName: '一',
        state:'已启用',
      },{
        name:'小学二年级',
        sName: '二',
        state:'已启用',
      },{
        name:'小学三年级',
        sName: '三',
        state:'已启用',
      }],
    }
  },
  methods:{
    add3(){this.visible3=true;this.title3='新增年级';},
    edit3(i,row){this.visible3=true;this.title3='年级信息修改';},
    delete3(i,row){
      this.$message({
        message: '年级信息删除成功！',
        type: 'success',
      });
    },
    submit3(i,row){this.visible3=false;
      this.$message({
        message: '年级信息提交成功！',
        type: 'success',
      });},
    cancle3(){this.visible3=false;},
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },

  },
}
</script>

<style scoped>
  .icon {
    cursor: pointer;
    font-size: 20px;
  }
  .table{
    overflow:auto;
  }
  .dialog .el-input{
    width:430px;
  }
  .dialog-footer{
    text-align:right;
  }
</style>


