<template>
  <div class="curve">
    <div>
      <el-row>
        <el-col :span="24">
          <div class="grid-content bg-purple">
            <el-form :inline="true" :data="form" class="demo-form-inline form" size='mini'>
              <el-row>
                <el-col :span='8'>
                  <el-form-item>
                    <el-col :span="11">
                      <el-date-picker type="date" placeholder="选择开始日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
                    </el-col>
                    <el-col :span="2">
                      <el-row type='flex' justify='center'>-</el-row>
                    </el-col>
                    <el-col :span="11">
                      <el-date-picker type="date" placeholder="选择结束日期" v-model="form.date2" style="width: 100%;"></el-date-picker>
                    </el-col>
                  </el-form-item>
                </el-col>
                <el-col :span='4'>
                  <el-form-item>
                    <el-select v-model="form.grade" placeholder="年级" >
                      <el-option label="四年级" value="四年级"></el-option>
                      <el-option label="五年级" value="五年级"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span='4'>
                  <el-form-item>
                    <el-select v-model="form.clazz" placeholder="班级" >
                      <el-option label="一班" value="一班"></el-option>
                      <el-option label="二班" value="二班"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span='4'>
                  <el-form-item>
                    <el-select v-model="form.course" placeholder='课程'> 
                      <el-option label="体育课程 16：00-16:45" value="体育课程 16：00-16:45"></el-option>
                      <el-option label="活动课程 10：00-10:20" value="活动课程 10：00-10:20"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                <el-col :span='4'>
                  <el-form-item>
                    <el-button type="text" size="medium" @click='remark'>备注上课内容</el-button>
                  </el-form-item>
                </el-col>
              </el-row>
            </el-form>
          </div>
        </el-col> 
        <el-col :span="5"></el-col>  
      </el-row>
      <el-row>
        <el-col :span='19'>
          <el-row>
            <el-col :span='10'>
              <el-row>
                <el-col :span='8' class='font'>班级最高心率：<a>138</a></el-col>
                <el-col :span='8' class='font'>班级最低心率：<a>75</a></el-col>
                <el-col :span='8' class='font'>班级平均心率：<a>95</a></el-col>
              </el-row>
            </el-col>
            <el-col :span='3' class='font'>阶段一心率：<a>95</a></el-col>
            <el-col :span='3' class='font'>阶段二心率：<a>105</a></el-col>
            <el-col :span='3' class='font'>阶段三心率：<a>115</a></el-col>
            <el-col :span='5'>
              <el-row type='flex'>
                <el-select v-model="form.rate" size='mini' placeholder="班级心率曲线" @change='change'>
                  <el-option label="班级心率曲线" value="班级心率曲线"></el-option>
                  <el-option label="个人心率曲线" value="个人心率曲线"></el-option>
                </el-select>
              </el-row>
            </el-col>
          </el-row>
          <el-row class='container'>
            <div class="two">
            <!-- 1. 图标容器 -->
              <div id="main" style="height:400px;"></div>
              <div id="main2" style="height:400px;"></div>
            </div>
          </el-row>
        </el-col>
        <el-col :span="5">
          <el-row type='flex' justify='end' id='right2'>
            <el-col :span='22'>
              <el-row>
                <el-col :span='16'>学生列表</el-col>
              </el-row>
              <el-row class='stuList'>
                <el-col :span='24'>
                  <el-row class='height'>
                    <el-col :span='12'><b class='primary'>吕迎迎</b></el-col>
                    <el-col :span='12'><b>李四</b></el-col>
                  </el-row>
                  <el-row class='height'>
                    <el-col :span='12'><b>王五</b></el-col>
                    <el-col :span='12'><b>赵六</b></el-col>
                  </el-row>
                  <el-row class='height'>
                    <el-col :span='12'><b>张三</b></el-col>
                    <el-col :span='12'><b>李四</b></el-col>
                  </el-row>
                  <el-row class='height'>
                    <el-col :span='12'><b>王五</b></el-col>
                    <el-col :span='12'><b>赵六</b></el-col>
                  </el-row>
                </el-col>
              </el-row>
            </el-col> 
          </el-row> 
        </el-col>  
      </el-row>
    </div>
  </div>
</template>

<script>
//2. 导入echarts库
let echarts = require('echarts');
require('echarts/lib/component/tooltip');
require('echarts/lib/component/title');
import $ from 'jquery';

var bool = false;
export default {
  name: 'curve',  
  data(){
    return{
      courseTime:'',
      formInline:'',
      form:{rate:'班级心率曲线'},
      student:'吕迎迎',
    }
  },
  mounted(){
    $('.stuList .height b').click(function(){
      $('.stuList .height b').removeAttr('class');
      $(this).attr('class','primary');
      this.student = $('this').text();
    });
    this.move1();
    this.move2();
    this.ifBool();
  },
  methods:{
    change(){
      bool = !bool;
      this.ifBool();
    },
    ifBool(){
      if(bool == false){
        $('#main2').css('display','none');
        $('#main').css('display','block');
        $('#right2').css('visibility','hidden');
        this.form.rate = '班级心率曲线';
      }else{
        $('#main').css('display','none');
        $('#main2').css('display','block');
        $('#right2').css('visibility','visible');
        this.form.rate = '个人心率曲线';
      }
    },
    curvePicture(){
      var colors = ['green','blue','red'];
      var arr = [],arr1 = [],arr2 = [],arr3 = [];
      var arrx = [],arry1 = [],arry2 = [],arry3 = [];
      var start = 0,end = 31;
      var rateText = '班级心率曲线';
      var rateSubtext = '四年级 1班 体育课程';
      var myChart = echarts.init($('#main')[0]);
      for(var k=0;k<=602;k=k+2){
        arr.push(k);
      }
      for(var k=0;k<301;k++){
        var random1 = Math.floor(Math.random()*199+1);
        var random2 = Math.floor(Math.random()*199+1);
        var random3 = Math.floor(Math.random()*199+1);
        arr1.push(random1);
        arr2.push(random2);
        arr3.push(random3);
      }
      function sw(){
        arrx = [],arry1 = [],arry2 = [],arry3 = [];
        for(var i=start;i<end;i++){
          arrx.push(arr[i]);
          arry1.push(arr1[i]);
          arry2.push(arr2[i]);
          arry3.push(arr3[i]);
        }
      }
      function time(){//获取当前时间。
        var date = new Date();
        date = date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()+" &nbsp; "+repair(date.getHours())+':'+repair(date.getMinutes());
        return date;
      }
      function repair(n){//时间补0函数
        n = n + '';
        return '00'.slice(0,2-n.length)+n;
      }
      // 绘制图表
      function curve(){
        if(end<arr.length){
          sw();
          start++;
          end++;
          var option = {
            color: colors,
            title:{
              text:rateText,
              left:'center',
              top:'10',
              subtext:rateSubtext,
              subtextStyle:{
                color:'#aaa',
                fontWeight:'bolder',
                align:'left',
              },
            },
            tooltip: {
              show:true,
              trigger:'axis',
              axisPointer:'shadow',
              snap:'snap',
              formatter: function(params){
                var date = time();
                var str = date + '<br/>' + '班级平均心率：' + params[0].value + '次/秒<br/>' + '男生平均心率：' + params[1].value + '次/秒<br/>' + '女生平均心率：' + params[2].value + '次/秒';
                return str;
              },
              textStyle:{//文本样式。
                color:['white'],
              },
            },
            legend: {
              data:['班级平均心率','男生平均心率','女生平均心率'],
              x:'right',
              top:35,
            },
            calculable : true,
            grid: {  //图表的内外边距
              top: 120,
              bottom: 50,
            },
            xAxis: [{  //X轴坐标参数
              name:'时间/秒',
              type: 'category',
              boundaryGap : false, //分界线范围
              data: arrx,
            }],
            yAxis: [{
              name:'心率/次数',
              type: 'value',
            }],
            series: [{
              name:'班级平均心率',
              type:'line',
              data: arry1,
              markPoint : {
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:'平均值'}
                ]
              },
            },{    
              name:'男生平均心率',
              type:'line',
              data: arry2,
              markPoint : {
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:'平均值'}
                ]
              },
            },{
              name:'女生平均心率',
              type:'line',
              data: arry3,
              markPoint : {
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:'平均值'}
                ]
              },
            }],
          }
          myChart.setOption(option);
        }
      }
      return curve;
    },
    curvePicture2(){
      var colors = ['#3300ff','#00ff99'];
      var myChart = echarts.init(document.getElementById('main2'));
      var arr = [],arr1 = [],arr2 = [];
      var arrx = [],arry1 = [],arry2 = [];
      var start = 0,end = 31;
      for(var k=0;k<=602;k=k+2){
        arr.push(k);
      }
      for(var k=0;k<301;k++){
        var random1 = Math.floor(Math.random()*199+1);
        var random2 = Math.floor(Math.random()*199+1);
        arr1.push(random1);
        arr2.push(random2);
      }
      function sw(){
        arrx = [],arry1 = [],arry2 = [];
        for(var i=start;i<end;i++){
          arrx.push(arr[i]);
          arry1.push(arr1[i]);
          arry2.push(arr2[i]);
        }
      }
      function time(){//获取当前时间。
        var date = new Date();
        date = date.getFullYear()+"-"+(date.getMonth()+1)+"-"+date.getDate()+" &nbsp; "+repair(date.getHours())+':'+repair(date.getMinutes());
        return date;
      }
      function repair(n){//时间补0函数
        n = n + '';
        var str = '00';
        return str.slice(0,2-n.length)+n;
      }
      // 绘制图表
      var vm = this;
      function curve(){
        if(end<arr.length){
          sw();
          start++;
          end++;
          var option = {
            color: colors,
            title:{
              text:'个人心率曲线',
              left:'center',
              top:'10',
              subtext:'四年级  web1701班  '+vm.student,
              subtextStyle:{
                color:'#aaa',
                fontWeight:'bolder',
                align:'left'
              },
            },
            tooltip: {
              show:true,
              trigger:'axis',
              axisPointer:'shadow',
              snap:'snap',
              formatter: function(params){
                var date = time();
                var str = date + '<br/>' + '班级平均心率：' + params[0].value + '次/秒<br/>' + '个人实时心率：' + params[1].value + '次/秒';
                return str;
              },
              textStyle:{//文本样式。
                color:['white'],
              },
            },
            legend: {
              data:['班级平均心率','个人实时心率'],
              x:'right',
              top:35,
            },
            calculable : true,
            grid: {  //图表的内外边距
              top: 120,
              bottom: 50,
            },
            xAxis: [{  //X轴坐标参数
              name:'时间/秒',
              type: 'category',
              boundaryGap : false, //分界线范围
              data: arrx,
            }],
            yAxis: [{
              name:'心率/次数',
              type: 'value',
            }],
            series: [{
              name:'班级平均心率',
              type:'line',
              data: arry1,
              markPoint : {
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:'平均值'}
                ]
              },
            },{    
              name:'个人实时心率',
              type:'line',
              data: arry2,
              markPoint : {
                data : [
                  {type : 'max', name: '最大值'},
                  {type : 'min', name: '最小值'}
                ]
              },
              markLine : {
                data : [
                  {type :'average', name:'平均值'}
                ]
              },
            }],
          }
          myChart.setOption(option);
        }
      }
      return curve;
    },
    move1(){
      var curve = this.curvePicture();
      setInterval(function(){
        curve();
      },2000);
      curve();//预先执行。
    },
    move2(){
      var curve = this.curvePicture2();
      setInterval(function(){
        curve();
      },2000);
      curve();//预先执行。
    },
    remark(){
      this.$message({
        message:'上课内容备注成功！',
        type:'success',
      });
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  *{
    margin:0;
    padding:0;
    font-size:12px;
  }
  .form{
    height:38px;
  }
  .font{
    display:inline-block;
    height:30px;
    line-height:30px;
    font-size:12px;
  }
  #remark{
    margin-top: 5px;
  }
  .container .two{
    border: 1px solid black;
    margin-top: 20px;
    box-shadow: 10px 10px 10px #999;
  }
  .stuList{
    height:404px;
    border:1px solid #999;
    box-shadow:4px 4px 6px #999;
    margin-bottom:20px;
    margin-top:5px;
  }
  #right2{
    margin-top:30px;
  }
  .height{
    height:30px;
    line-height:30px;
    text-align:center;
  }
  .primary{
    background:#169bd5;
    color:white;
    border-radius:4px;
  }
  .stuList .el-row .el-col{
    cursor:pointer;
  }
  .stuList .el-row .el-col b{
    display:inline-block;
    width:100%;
  }
  
</style>



