<template>
  <div class="timeTable">
    <!-- <el-row>
      <el-col :span='12'></el-col>
      <el-col :span='12'>
        <el-row type='flex' justify='end'>
          
        </el-row>
      </el-col>
    </el-row> -->
    <el-row>
      <el-col :span='12'>
        <el-form inline :data='form2' size='small'>
          <el-form-item label='年级：'>
            <el-select v-model="form2.grade" placeholder="请选择年级">
              <el-option label="一年级" value="一年级"></el-option>
              <el-option label="二年级" value="二年级"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col :span='12'>
        <el-row type='flex' justify='end'>
          <el-button type='info' size='small' @click='save2'>保存</el-button>
        </el-row>
      </el-col>
    </el-row>
    <el-table :data="time" style="width: 100%" height="250" stripe :header-row-style="rowClass">
      <el-table-column prop="clazz" label="班级">
      </el-table-column>
      <el-table-column prop="course" label="体育课程">
        <template slot-scope='scope'>
          <el-input v-model='scope.row.course' size='small' style='width:200px' @focus='select2'></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="active" label="课间活动">
        <template slot-scope='scope'>
          <el-input v-model='scope.row.active' size='small' style='width:200px' @focus='select2'></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="active" label="活动介绍">
        <template slot-scope='scope'>
          <el-input v-model='scope.row.active2' size='small' style='width:200px' @focus='select2'></el-input>
        </template>
      </el-table-column>
      <!-- <el-table-column label="操作" width="160">
        <template slot-scope='scope'>
          <i class="icon el-icon-edit" @click="edit2(scope.$index, scope.row)"></i>
          <i class="icon el-icon-delete" @click="delete2(scope.$index, scope.row)"></i>
        </template>
      </el-table-column> -->
    </el-table>

    <el-dialog :title="title" :visible.sync="visible">
      <el-form :model="form">
        <a></a><el-checkbox v-model="form.th1">教师一</el-checkbox>
        <b></b><el-checkbox v-model="form.th2">教师二</el-checkbox>
        <b></b><el-checkbox v-model="form.th3">教师三</el-checkbox>
        <b></b><el-checkbox v-model="form.th1">教师一</el-checkbox>
        <b></b><el-checkbox v-model="form.th2">教师二</el-checkbox>
        <b></b><el-checkbox v-model="form.th3">教师三</el-checkbox><br><br>
        <a></a><el-checkbox v-model="form.th1">教师一</el-checkbox>
        <b></b><el-checkbox v-model="form.th2">教师二</el-checkbox>
        <b></b><el-checkbox v-model="form.th3">教师三</el-checkbox>
        <b></b><el-checkbox v-model="form.th1">教师一</el-checkbox>
        <b></b><el-checkbox v-model="form.th2">教师二</el-checkbox>
        <b></b><el-checkbox v-model="form.th3">教师三</el-checkbox><br><br>
        <a></a><el-checkbox v-model="form.th1">教师一</el-checkbox>
        <b></b><el-checkbox v-model="form.th2">教师二</el-checkbox>
        <b></b><el-checkbox v-model="form.th3">教师三</el-checkbox>
        <b></b><el-checkbox v-model="form.th1">教师一</el-checkbox>
        <b></b><el-checkbox v-model="form.th2">教师二</el-checkbox>
        <b></b><el-checkbox v-model="form.th3">教师三</el-checkbox><br><br>
      </el-form>
      <div slot="footer" class="dialog-footer">
         <el-button @click="cancle2">取 消</el-button>
         <el-button type="primary" @click="submit2">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'timeTable',
  data(){
    return {
      form:{},
      form2:{},
      visible:false,
      title:'选择教师',
      time:[{
        clazz:'1班',
      },{
        clazz:'2班',
      },{
        clazz:'3班',
      }],
    }
  },
  methods:{
    select2(){this.visible=true;},
    edit2(){this.visible=true;},
    delete2(){
      this.$message({
        message: '信息已成功删除！',
        type: 'success',
      });
    },
    save2(){
      this.$message({
        message: '保存成功！',
        type: 'success',
      });
    },
    cancle2(){this.visible=false;},
    submit2(){
      this.$message({
        message: '该条信息已成功提交！',
        type: 'success',
      });
      this.visible=false;
    },
    rowClass(row, index) {
      return {"background-color":"#A5B0BB",'color':'white'}
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .icon {
    cursor: pointer;
    font-size: 20px;
  }
  a,b{
    display:inline-block;
    width:20px;
  }
</style>



